html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.icon-lock {
  width: 0.83em;
  height: 1em;
  fill: #010101;
}

.icon-plane {
  width: 1em;
  height: 1em;
  fill: #333;
}

body {
  background: #FFF;
  line-height: 1.2;
  font-size: 18px;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
}

a {
  text-decoration: none;
}

strong {
  font-weight: 700;
}

.title-lg {
  color: #fff;
  text-align: center;
  font-family: Roboto;
  font-size: 48px;
}

.title-lg, .title-decor, .title-md {
  font-weight: 700;
}

.title-md {
  color: #939393;
  font-size: 30px;
  margin-bottom: 30px;
}

.title-md--green {
  color: #94c34a;
}

.title-m {
  color: #939393;
  font-size: 24px;
  margin-bottom: 25px;
}

.title-decor {
  margin-bottom: 40px;
  font-size: 30px;
  color: #828282;
  position: relative;
}

.title-decor:after {
  content: "";
  position: absolute;
  bottom: -6px;
  left: 0;
  width: 51px;
  height: 1px;
  background-color: #464545;
}

.article p {
  margin-bottom: 18px;
}

.article p:last-of-type {
  margin-bottom: 0;
}

.decor-block {
  position: relative;
  padding-left: 18px;
  font-size: 20px;
}

.decor-block:before {
  content: "";
  position: absolute;
  left: 0;
  top: 3px;
  bottom: 3px;
  width: 4px;
  background-color: #828282;
}

.list li {
  position: relative;
  padding-left: 21px;
  margin-bottom: 17px;
}

.list li:before {
  content: "";
  position: absolute;
  left: 0;
  top: 6px;
  width: 8px;
  height: 8px;
  background-color: #828282;
  border-radius: 50%;
}

.list li:last-of-type {
  margin-bottom: 0;
}

.l {
  max-width: 1170px;
  padding: 0 15px;
  margin-left: auto;
  margin-right: auto;
}

.block {
  display: flex;
}

.header {
  padding: 0 5px;
  font-size: 0;
  background: #605f5f;
}

.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 56px;
}

.header__right {
  display: inline-flex;
  justify-content: flex-end;
  align-items: center;
}

.header__prices li {
  display: inline-block;
  vertical-align: top;
  margin-right: 31px;
  color: #fff;
  font-size: 24px;
}

.header__logo {
  display: inline-block;
  vertical-align: top;
}

.header__logo img {
  max-width: 195px;
  height: auto;
  max-height: 34px;
}

.header__user {
  display: inline-flex;
  justify-content: flex-end;
  align-items: center;
  margin-left: 84px;
}

.header__user-inner a, .header__user-inner span {
  font-size: 24px;
  font-weight: 400;
  display: inline-block;
  vertical-align: top;
  color: #fff;
  margin-left: 25px;
}

.header__user-inner a {
  transition: color 0.3s;
}

.header__user-inner a.is-active {
  color: #94c34a;
}

@media only screen and (min-width: 1024px) {
  .header__user-inner a:hover {
    color: #94c34a;
  }
}

.lang-list {
  display: inline-block;
  vertical-align: top;
}

.lang-list img {
  width: 30px;
  height: 20px;
}

.lang-list li {
  display: inline-block;
  vertical-align: top;
  margin-right: 19px;
}

.lang-list li:last-of-type {
  margin-right: 0;
}

.lang-list a {
  display: block;
}

.soc-btn {
  appearance: none;
  background: transparent;
  border-radius: 50%;
  width: 38px;
  height: 38px;
  border: 1px solid #fff;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  transition: border-color 0.3s;
}

.soc-btn svg {
  fill: #fff;
  font-size: 19px;
  transition: fill 0.3s;
}

@media only screen and (min-width: 1024px) {
  .soc-btn:hover {
    border-color: #94c34a;
  }
  .soc-btn:hover svg {
    fill: #94c34a;
  }
}

.contact-btn {
  background-image: url("../img/sprite.png");
  width: 282px;
  height: 59px;
  background-position: 0px 0px;
  appearance: none;
  background-color: transparent;
  border: none;
  text-align: center;
  padding: 14px 20px  14px 40px;
  position: relative;
  top: 15px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.contact-btn span {
  color: #fff;
  font-size: 24px;
  font-weight: 400;
  display: inline-block;
  vertical-align: top;
  margin-right: 50px;
}

.contact-btn svg {
  font-size: 20px;
  fill: #fff;
}

.btn {
  appearance: none;
  background: transparent;
  display: inline-block;
  vertical-align: top;
  color: #78a92b;
  font-size: 18px;
  font-weight: 400;
  min-width: 169px;
  border: 2px solid rgba(120, 169, 43, 0.56);
  transition: background 0.3s, color 0.3s;
  border-radius: 25px;
  text-align: center;
  padding: 8px 10px;
}

@media only screen and (min-width: 1024px) {
  .btn:hover {
    background: #78a92b;
    color: #fff;
  }
}

.btn--big {
  min-width: 243px;
  padding: 10px 10px;
}

.nav {
  position: relative;
  padding-top: 19px;
  padding-bottom: 22px;
}

.nav__bg {
  position: absolute;
  z-index: -1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

.nav__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.nav a {
  display: inline-block;
  vertical-align: top;
  color: #fff;
  font-size: 24px;
  transition: background 0.3s;
  padding: 3px 14px;
  border: 1px solid #fff;
  border-radius: 20px;
}

.nav a.is-active {
  background: #40630a;
}

@media only screen and (min-width: 1024px) {
  .nav a:hover {
    background: #40630a;
  }
}

.footer {
  background: #605f5f;
  padding-top: 25px;
  padding-bottom: 15px;
  font-size: 0;
}

.footer__inner {
  *zoom: 1;
}

.footer__inner:after {
  content: " ";
  display: table;
  clear: both;
}

.footer__left {
  float: left;
  margin-right: 112px;
}

.footer__center {
  float: left;
}

.footer__right {
  float: right;
  text-align: right;
}

.footer__logo {
  max-width: 262px;
  max-height: 45px;
  margin-bottom: 28px;
  display: inline-block;
  vertical-align: top;
}

.footer__logo img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.footer__nav {
  margin-bottom: 35px;
}

.footer__nav li {
  display: inline-block;
  vertical-align: top;
  margin-right: 45px;
}

.footer__nav li:last-of-type {
  margin-right: 0;
}

.footer__nav a {
  font-size: 18px;
  color: #fff;
  transition: color 0.3s;
}

.footer__nav a.is-active {
  color: #94c34a;
  text-decoration: underline;
}

@media only screen and (min-width: 1024px) {
  .footer__nav a:hover {
    color: #94c34a;
  }
}

.copyright {
  color: #000;
  font-size: 14px;
  font-weight: 400;
}

.accept-block {
  font-size: 0;
}

.accept-block p {
  color: #fff;
  font-size: 18px;
  margin-bottom: 24px;
}

.accept-block li {
  display: inline-block;
  vertical-align: top;
  margin-right: 9px;
}

.accept-block li:last-of-type {
  margin-right: 0;
}

.accept-block li img {
  width: 48px;
  height: 48px;
}

.hero {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  padding-top: 85px;
  padding-bottom: 5px;
  text-align: center;
  margin-bottom: 50px;
}

.hero__logo {
  display: inline-block;
  vertical-align: top;
  margin-bottom: 75px;
  max-width: 100%;
}

@media only screen and (max-width: 1023px) {
  .hero__logo {
    display: none;
  }
}

.hero__logo-mob {
  display: none;
}

@media only screen and (max-width: 1023px) {
  .hero__logo-mob {
    display: inline-block;
    vertical-align: top;
    margin-bottom: 75px;
  }
}

.hero .title-lg {
  margin-bottom: 82px;
}

.welcome {
  padding-bottom: 75px;
}

.welcome__inner {
  display: flex;
}

.welcome__left {
  flex-basis: 57.36842%;
  flex-grow: 0;
  flex-shrink: 1;
}

.welcome__left .decor-block {
  margin-bottom: 60px;
}

.welcome__right {
  flex-grow: 1;
  flex-shrink: 0;
}

.wplate {
  padding: 0px 50px 0;
  position: relative;
  top: -10px;
}

.wplate__item {
  width: 204px;
  text-align: center;
  padding: 21px 10px 21px;
  position: relative;
  margin-bottom: 36px;
  margin-left: auto;
  margin-right: auto;
}

.wplate__item:before {
  content: "";
  position: absolute;
  width: 260px;
  height: 100%;
  top: 0;
  left: 50%;
  margin-left: -130px;
  background-color: #828282;
  transform: skew(-20deg);
  z-index: -1;
}

.wplate__item span {
  color: #fff;
  font-size: 20px;
}

.wplate__item img {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: -108px;
}

.wplate__item--img-r img {
  left: auto;
  right: -108px;
}

.founders {
  background-color: #fcfcfc;
  border-top: 1px solid #94c34a;
  border-bottom: 1px solid #94c34a;
  padding-top: 45px;
  padding-bottom: 50px;
  margin-bottom: 40px;
}

.founders__inner {
  display: flex;
}

.founders__inner .founder {
  flex-basis: calc(100% - 60px);
  margin-right: 30px;
}

.founders__inner .founder:nth-of-type(3n) {
  margin-right: 0;
}

.founders .title-decor {
  margin-bottom: 97px;
}

.founder {
  display: inline-flex;
  flex-direction: column;
  margin-bottom: 30px;
  border-bottom: 1px solid #94c34a;
  border-right: 1px solid #94c34a;
  border-top: 1px solid #94c34a;
  align-items: flex-start;
}

.founder__name {
  padding-right: 25px;
  background: #fcfcfc;
  position: relative;
  top: -20px;
  display: inline-block;
  vertical-align: top;
  margin-bottom: 0;
  flex-grow: 0;
}

.founder__position {
  color: #94c34a;
  position: relative;
  top: -11px;
  flex-grow: 0;
}

.founder__text {
  position: relative;
  padding: 0 25px 25px 25px;
  flex-grow: 1;
  width: 100%;
  font-size: 20px;
}

.founder__text:before, .founder__text:after {
  content: "";
  position: absolute;
}

.founder__text:before {
  width: 16px;
  height: 13px;
  background-image: url("../img/quote.png");
  background-size: contain;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  left: 0;
  top: 3px;
}

.founder__text:after {
  height: calc(100% - 22px);
  left: 0;
  bottom: 0;
  width: 1px;
  background: #94c34a;
}

.refferals {
  margin-bottom: 50px;
}

.refferals__inner {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.refferals__left {
  flex-basis: calc(50% - 15px);
  padding-top: 13px;
}

.refferals__right {
  flex-basis: calc(50% - 15px);
}

.refferals__right .decor-block {
  margin-bottom: 47px;
}

.refferals__wrap {
  display: flex;
  align-items: flex-end;
}

.refferals__wrap .list {
  width: 50%;
  padding-right: 15px;
  margin-bottom: 10px;
}

.refferals__security {
  width: 50%;
  text-align: center;
}

.refferals .title-decor {
  margin-bottom: 43px;
}

.security-block {
  width: 100%;
  background-color: #fff;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
  color: #94c34a;
  font-size: 16px;
  margin-bottom: 18px;
  text-align: center;
  padding: 14px 5px;
}

.security-block svg {
  display: inline-block;
  vertical-align: top;
  margin-right: 15px;
  font-size: 17px;
  fill: #94c34a;
}

.ref-list__item {
  padding-left: 30px;
  padding-top: 25px;
  padding-bottom: 25px;
  position: relative;
  width: 400px;
  border-radius: 50px 0 0 50px;
  border: 2px solid #94c34a;
  margin-bottom: 24px;
}

.ref-list__item:last-of-type {
  margin-bottom: 0;
}

.ref-list__item span {
  font-size: 23px;
}

.ref-list__item--middle {
  width: 465px;
}

.ref-list__item--middle .ref-list__img {
  right: -59px;
}

.ref-list__item--top .ref-list__img {
  transform: translateY(-52%);
}

.ref-list__item--bottom .ref-list__img {
  transform: translateY(-48%);
}

.ref-list__img {
  width: 90px;
  height: 90px;
  background-color: #fff;
  border: 1px solid #94c34a;
  border-radius: 50%;
  overflow: hidden;
  display: block;
  position: absolute;
  right: -48px;
  top: 50%;
  transform: translateY(-50%);
}

.ref-list__img img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.logos {
  padding-top: 45px;
  padding-bottom: 38px;
  background: #94c34a;
}

.logos__inner {
  display: flex;
  align-items: flex-center;
}

.logos__item {
  margin-bottom: 20px;
  margin-right: 30px;
  flex-grow: 1;
}

.logos__item:nth-of-type(5n) {
  margin-right: 0;
}

.logos__item img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
