.footer {
	background: $gray2;
	padding-top: 25px;
	padding-bottom: 15px;
	font-size: 0;
	&__inner {
		@include clr; }
	&__left {
		float: left;
		margin-right: 112px; }
	&__center {
		float: left; }
	&__right {
		float: right;
		text-align: right; }
	&__logo {
		max-width: 262px;
		max-height: 45px;
		margin-bottom: 28px;
		display: inline-block;
		vertical-align: top;
		img {
			width: 100%;
			height: 100%;
			object-fit: contain; } }
	&__nav {
		margin-bottom: 35px;
		li {
			display: inline-block;
			vertical-align: top;
			margin-right: 45px;
			&:last-of-type {
				margin-right: 0; } }
		a {
			font-size: 18px;
			color: $white;
			transition: color 0.3s;
			&.is-active {
				color: $green;
				text-decoration: underline; }
			@include mm {
				&:hover {
					color: $green; } } } } }

.copyright {
	color: $black;
	font-size: 14px;
	font-weight: 400; }
.accept-block {
	font-size: 0;
	p {
		color: $white;
		font-size: 18px;
		margin-bottom: 24px; }
	ul {}
	li {
		display: inline-block;
		vertical-align: top;
		margin-right: 9px;
		&:last-of-type {
			margin-right: 0; }
		img {
			width: 48px;
			height: 48px; } } }
