.hero {
	background-size: cover;
	background-repeat: no-repeat;
	background-position: 50% 50%;
	padding-top: 85px;
	padding-bottom: 5px;
	text-align: center;
	margin-bottom: 50px;
	&__logo {
		display: inline-block;
		vertical-align: top;
		margin-bottom: 75px;
		max-width: 100%;
		@include md {
			display: none; } }
	&__logo-mob {
		display: none;
		@include md {
			display: inline-block;
			vertical-align: top;
			margin-bottom: 75px; } }
	.title-lg {
		margin-bottom: 82px; } }
.welcome {
	padding-bottom: 75px;
	&__inner {
		display: flex; }
	&__left {
		flex-basis: percentage(654/1140);
		flex-grow: 0;
		flex-shrink: 1;
		.decor-block {
			margin-bottom: 60px; } }
	&__right {
		flex-grow: 1;
		flex-shrink: 0; } }
.wplate {
	padding: 0px 50px 0;
	position: relative;
	top: -10px;
	&__item {
		width: 204px;
		text-align: center;
		padding: 21px 10px 21px;
		position: relative;
		margin-bottom: 36px;
		margin-left: auto;
		margin-right: auto;
		&:before {
			content: "";
			position: absolute;
			width: 260px;
			height: 100%;
			top: 0;
			left: 50%;
			margin-left: -130px;
			background-color: $gray;
			transform: skew(-20deg);
			z-index: -1; }
		span {
			color: $white;
			font-size: 20px; }
		img {
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			left: -108px; }
		&--img-r {
			img {
				left: auto;
				right: -108px; } } } }
.founders {
	background-color: #fcfcfc;
	border-top: 1px solid $green;
	border-bottom: 1px solid $green;
	padding-top: 45px;
	padding-bottom: 50px;
	margin-bottom: 40px;
	&__inner {
		display: flex;
		.founder {
			flex-basis: calc(100% - 60px);
			margin-right: 30px;
			&:nth-of-type(3n) {
				margin-right: 0; } } }
	.title-decor {
		margin-bottom: 97px; } }
.founder {
	display: inline-flex;
	flex-direction: column;
	margin-bottom: 30px;
	border-bottom: 1px solid $green;
	border-right: 1px solid $green;
	border-top: 1px solid $green;
	align-items: flex-start;
	&__name {
		padding-right: 25px;
		background: #fcfcfc;
		position: relative;
		top: -20px;
		display: inline-block;
		vertical-align: top;
		margin-bottom: 0;
		flex-grow: 0; }
	&__position {
		color: $green;
		position: relative;
		top: -11px;
		flex-grow: 0; }
	&__text {
		position: relative;
		padding: 0 25px 25px 25px;
		flex-grow: 1;
		width: 100%;
		font-size: 20px;
		&:before, &:after {
			content: "";
			position: absolute; }
		&:before {
				width: 16px;
				height: 13px;
				background-image: url('../img/quote.png');
				background-size: contain;
				background-position: 50% 50%;
				background-repeat: no-repeat;
				left: 0;
				top: 3px; }
		&:after {
			height: calc(100% - 22px);
			left: 0;
			bottom: 0;
			width: 1px;
			background: $green; } } }
.refferals {
	margin-bottom: 50px;
	&__inner {
		display: flex;
		justify-content: space-between;
		align-items: flex-start; }
	&__left {
		flex-basis: calc(50% - 15px);
		padding-top: 13px; }
	&__right {
		flex-basis: calc(50% - 15px);
		.decor-block {
			margin-bottom: 47px; } }
	&__wrap {
		display: flex;
		align-items: flex-end;
		.list {
			width: 50%;
			padding-right: 15px;
			margin-bottom: 10px; } }
	&__security {
		width: 50%;
		text-align: center; }
	.title-decor {
		margin-bottom: 43px; } }
.security-block {
		width: 100%;
		background-color: $white;
		box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
		color: $green;
		font-size: 16px;
		margin-bottom: 18px;
		text-align: center;
		padding: 14px 5px;
		svg {
			display: inline-block;
			vertical-align: top;
			margin-right: 15px;
			font-size: 17px;
			fill: $green; } }
.ref-list {

	&__item {
		padding-left: 30px;
		padding-top: 25px;
		padding-bottom: 25px;
		position: relative;
		width: 400px;
		border-radius: 50px 0 0 50px;
		border: 2px solid $green;
		margin-bottom: 24px;
		&:last-of-type {
			margin-bottom: 0; }
		span {
			font-size: 23px; }
		&--middle {
			width: 465px;
			.ref-list__img {
				right: -59px; } }
		&--top {
			.ref-list__img {
				transform: translateY(-52%); } }
		&--bottom {
			.ref-list__img {
				transform: translateY(-48%); } } }
	&__img {
		width: 90px;
		height: 90px;
		background-color: $white;
		border: 1px solid $green;
		border-radius: 50%;
		overflow: hidden;
		display: block;
		position: absolute;
		right: -48px;
		top: 50%;
		transform: translateY(-50%);
		img {
			width: 100%;
			height: 100%;
			object-fit: contain; } } }
.logos {
	padding-top: 45px;
	padding-bottom: 38px;
	background: $green;
	&__inner {
		display: flex;
		align-items: flex-center; }
	&__item {
		margin-bottom: 20px;
		margin-right: 30px;
		flex-grow: 1;
		&:nth-of-type(5n) {
			margin-right: 0; }
		img {
			width: 100%;
			height: 100%;
			object-fit: contain; } } }
